.hownd-itc {
  background-size: cover;
}
.hownd-itc__row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -20px;
  margin-left: -20px;
}
.hownd-itc__row .col-content,
.hownd-itc__row .col-image {
  padding-right: 20px;
  padding-left: 20px;
  flex: 0 0 100%;
}
.hownd-itc__row .col-content {
  font-size: 20px;
}
.hownd-itc__row .col-content .wp-block-buttons {
  margin-top: 20px;
}
@media screen and (min-width: 768px) {
  .hownd-itc__row .col-content,
  .hownd-itc__row .col-image {
    flex: 0 0 50%;
  }
  .hownd-itc__row.image--left .col-image {
    order: -1;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .hownd-itc.home-banner .col-image {
    flex: 0 0 40%;
  }
  .hownd-itc.home-banner .col-content {
    flex: 0 0 60%;
  }
  .hownd-itc.home-banner .col-content img {
    width: auto;
    max-height: 100px;
  }
  .hownd-itc.home-banner .col-content h2.has-xlarge-font-size {
    font-size: 20px !important;
  }
}
@media screen and (min-width: 1200px) {
  .hownd-itc h1 {
    font-size: 46px;
  }
}
@media screen and (max-width: 991px) {
  .hownd-itc:not(.home-banner) .col-content .wp-block-image img {
    width: auto !important;
    max-height: 150px;
  }
}
@media screen and (max-width: 767px) {
  .hownd-itc__row .col-content {
    text-align: center;
  }
  .hownd-itc__row .col-content h1 {
    font-size: 40px;
  }
  .hownd-itc__row .col-content h2 {
    font-size: 46px;
  }
  .hownd-itc__row .col-content .wp-block-buttons {
    justify-content: center;
  }
  .hownd-itc__row .col-image {
    margin-top: 30px;
  }
}